.jb-header{
  &__heading{
    text-align: center;
    @media screen and (min-width: $breakpoint-med){
      display: flex;
      justify-content: space-between;
    }
    h1, h2{
      display: flex;
      flex-direction: column;
      justify-content: space-around;
    }
  }
  & {
    display: inline-block;
    width: 100%;
    padding: 1em;
    margin: auto;
    background: linear-gradient(to right, $primary-color 0%,$secondary-color 50%,$primary-color 100% ); 
    background-size: $bgWidth 100%;
    animation: background-pulse 15s linear infinite ;
  }
}

@keyframes background-pulse{
  0%{
    background-position-x: 0;
  }

  100%{
    background-position-x: $bgWidth;
  }
}
